import React, { Component } from 'react';
import Slide from '../slide/slide';
import styles from './slider.css';
import Arrows from '../arrows/arrows';

export default class Slider extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this)
    this.state = {
        images : props.images ,
        displayedImageIndex : 0
    }
  }

  handleClick(event , direction ){
    event.stopPropagation();
    switch(direction){
        case 'right' :
            return this.setState({displayedImageIndex : this.returnArrayIndex(this.state.displayedImageIndex+1)});
        case 'left':
            return this.setState({displayedImageIndex : this.returnArrayIndex(this.state.displayedImageIndex-1)});
        default: return null;
    }
  }

  returnArrayIndex(index){
    if(this.state.images[index]){
        return index;
    }else{
        return index < 0 ? this.state.images.length - 1 : 0 ; 
    }
  }

  render() {
    return (
          this.state.images ? 
                (
                    <div className={styles.slider}>
                        <Slide path={this.props.path} image={this.state.images[this.state.displayedImageIndex] }/> 
                        <Arrows handleClick={this.handleClick} direction="right"/>
                        <Arrows handleClick={this.handleClick} direction="left"/>    
                    </div>): ''
                );
  }
}